<template>
  <div id = "dashboard" >
    <ul class="collection whit-header #ff5722 deep-orange">
      <li class="collection-header center">
        <h3>EMPLOYEES</h3>
        </li>
        <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item #757575 grey darken-1 white-text">
          {{employee.employee_id}}  {{employee.name}} <div class="chip"> {{employee.dept}} </div>

          <router-link class="secondary-content" v-bind:to="{name: 'viewemployee', params: {employee_id: employee.employee_id}}">
            <i class="fa fa-eye"></i>
          </router-link>
        </li>

    </ul>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
      <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>

</template>

<script>
import db from './firebaseInit'

export default {
  name: 'dashboard',
  data(){
    return{
        employees:[]
    }
  },
  created(){
    db.collection('employees').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        console.log(doc)
        const data = {
          'id': doc.id, 
          'employee_id': doc.data().employee_id,
          'name': doc.data().name,
          'dept': doc.data().dept,
          'position': doc.data().position        
        }
        this.employees.push(data)
      })
    })

  }

}
</script>



