<template>
  <div id = "newemployee">
    <h3>New Employee</h3>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="employee_id" required>
            <label>
              Employe ID #
            </label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
            <label>
              Employe Name
            </label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="dept" required>
            <label>
              Employe Department
            </label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="position" required>
            <label>
              Employe Postion
            </label>
          </div>
        </div>

        <button type="submit" class="btn">SUBMIT</button>
        <router-link to="/" class="btn grey">CANCEL
        </router-link>
      </form>
    </div>
  </div>

</template>

<script>
import db from './firebaseInit'
export default {
  name: 'newemployee',
  data(){
    return{
      employee_id: null,
      name: null,
      dept: null,
      position: null

    }
  },
  methods: {
    saveEmployee() {
    db.collection('employees').add({
      employee_id: this.employee_id,
      name: this.name,
      dept: this.dept,
      position: this.position
    }).then(docRef => {
      this.$router.push('/')
      .catch(error => console.log(err))
    })
  }
  }
}
</script>



